export default {
	data() {
		return {
			shareInfo:{
				title:'',
				poser:''
			},
		}
	},
	onShow() {
		this.isJump()
		this.getShareInfo()
	},
	onShareAppMessage(res) {
		if (res.from === 'button') {// 来自页面内分享按钮
		  console.log(res.target)
		}
		return {
		  title:this.shareInfo.title,
		  path: '/pages/index/index'
		}
	},
	methods: {
		isJump(){
			let self = this;
			let token = uni.getStorageSync('token')||''
			console.log(token)
			if(token){
				self.getStatus()
			}else{
				wx.login({
				  success (res) {
				    if (res.code) {
						console.log(self)
						self.$api.post('api/user/login',{
							code:res.code
						}).then((res)=>{
							if(res.access_token){
								self.hasToken=true
							}else{
								return
							}
							uni.setStorageSync('token',res.access_token)
							uni.setStorageSync('user_id',res.user_id)
							uni.setStorageSync('session_key',res.session_key)
							if (!res.first) {
								
								self.getStatus()
							}else{
								uni.setStorageSync('first',1)
							}
						})
				    } else {
				      console.log('登录失败！' + res.errMsg)
				    }
				  }
				 
				})
			}
		},
		//判断套餐状态
		getStatus(){
			this.$api.get('api/pray/show').then((res)=>{
				console.log(res)
				uni.setStorageSync('paryData',res.prayer)
				try{
					uni.setStorageSync('name',res.prayer.name)
				}catch(e){
					//TODO handle the exception
				}
				let detail={...res.dojo}
				detail.content=encodeURIComponent(detail.content)
				if(res.status==1){
					uni.redirectTo({
					    url:'/pages/package/package?detail='+JSON.stringify(detail)+'&status=1'
					});
				}else if(res.status==0){
					uni.redirectTo({
						url:'/pages/package/package?detail='+JSON.stringify(detail)+'&expired=1'
					})
				}
			})
		},
		getShareInfo(){
			this.$api.get('/api/user/share').then((res)=>{
				console.log(res)
				this.shareInfo=res
			})
		},
		goDojoList(){
			uni.navigateTo({
			    url: '/pages/dojoList/dojoList'
			});
		},
		goChoseDojo(){
			uni.navigateTo({
				url:'/pages/chooseDojo/chooseDojo'
			})
			// wx.navigateToMiniProgram({
			//   appId: 'wxe38308656658eec0',
			//   path: 'pages/index/index',
			//   extraData: {
			//     foo: 'bar'
			//   },
			//   envVersion: 'develop',
			//   success(res) {
			//     // 打开成功
			//   }
			// })
		}
	}
}
